<template>
    <Modal v-model="isShow"
           title="新增配置"
           ok-text="保存"
           @on-visible-change="modal_visible">
        <div>
            <Form ref="form_add"
                  :model="info"
                  :rules="info_rules"
                  :label-width="80">
                <FormItem label="key:" prop="key">
                    <Input v-model="info.keyValue">
                        <span slot="prepend">{{appName}}</span>
                    </Input>
                </FormItem>
                <FormItem label="描述:" prop="title">
                    <Input type="text" v-model="info.title" />
                </FormItem>
                <FormItem label="VALUE:" prop="value">
                    <Input type="textarea" v-model="info.value" />
                </FormItem>
            </Form>
        </div>
        <div slot="footer">
            <Button type="primary" @click="onClick_save">保存</Button>
        </div>
    </Modal>
</template>

<script>
    export default {
        name: "modalAdd_config",
        data() {
            return {
                isShow: false,
                info: {
                    appName: '',
                    keyValue: '',
                    key: '',
                    title: '',
                    value: ''
                },
                info_rules: {
                    key: [
                        { required: true, message: '配置key不能为空!', trigger: 'blur' },
                        { type: 'string' ,min: 4, max: 100, message: '配置Key长度限制为4~100!', trigger: 'blur' }
                    ],
                    title: [
                        { required: true, message: '描述不能为空!', trigger: 'blur' }
                    ]
                }
            };
        },
        props: {
            modalValue: {
                type: Boolean,
                required: true,
                default() {
                    return false;
                }
            },
            appName: {
                type: String,
                default() {}
            }
        },
        watch: {
            modalValue(val) {
                this.isShow = val;
            },
            appName(val) {
                this.info.appName = val;
            },
            'info.keyValue': {
                immediate: true,
                handler(val) {
                    this.info.key = this.info.appName + '.' + val;
                }
            }
        },
        methods: {
            modal_visible(value) {
                this.$emit('modal_visible_change', 'modal_value_add', value);
            },
            modal_close() {
                this.isShow = false;
                this.$emit('modal_visible_change', 'modal_value_add', false);
            },

            onClick_save() {
                var that = this;
                this.$refs['form_add'].validate((valid) => {

                    if(valid) {

                        that.$http({
                            method: 'get',
                            url: '/conf/add',
                            params: that.info
                        }).then(function (response) {
                            console.dir(response);
                            if(response.status === 1) {
                                that.$Message.success({
                                    content: '添加成功！'
                                })
                                that.$emit('modal_getData');
                                that.modal_close();
                            }
                            else {
                                that.$Message.error({
                                    content: response.msg
                                })
                            }
                        })

                    }
                    else {}
                })
            }
        }

    }
</script>
